'use strict';

export const data = {
  managerFields: {},

  managerFieldTypes: [
    {id: 'text', name: ' (~200 )'},
    {id: 'textarea', name: ' ()'},
    {id: 'number', name: ''},
    {id: 'date', name: ''},
  ],
}

export const watch = {}

export const computed = {}

export const methods = {
  addCustomField() {
    const rand = Math.random() * 100000 | 0,
          id = 'custom_' + rand,
          name = '' + rand;

    this.managerFields[id] = {name, type: 'text'};
  },
  removeCustomField(id) {
    delete this.managerFields[id];
  },
}
